.group-users {
    display: flex;
    flex-wrap: wrap;
    padding: 0px 0px;
    min-height: 60px;
    max-height: 210px;
    overflow-x: scroll;
}

.group-users::-webkit-scrollbar {
    display: none;
}

/* Hide scrollbar for a specific element */
.group-users::-webkit-scrollbar {
    display: none;
}


.text {
    padding: "0px";
    margin: '0px';
    font-size: '12px';

}
.info{
    padding: "0px";margin: '0px'; font-size: '12px';color: "#999"; line-height: '24px'
}


.notic::-webkit-scrollbar {
    display: none;
}
