.layout {
    height: 100%;
}

.menu {
    border-inline-end: "0px";
    height: 100%;
}

.kf-name {
    color: #FBFBFB;
    cursor: default;
}

.menu-top {
    background-color: #001529;
    padding: 10px 0 10px 4px;
    text-align: center;
}

.menu-top:hover {
    cursor: default;
    background-color: #001B33;
}

.users {
    height: 100vh;
    margin-left: 8px;
    padding: 0px 10px;
    background-color: #FBFBFD;
    margin-top: 4px;
    border-radius: 4px;
}

.chat {
    margin-left: 4px;
    margin-right: 4px;
    margin-top: 4px;
}


.chat-sider {
    margin-right: 4px;
    margin-top: 4px;
}
.my-floating-card {
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.3s ease;
  animation: jump 3s infinite; /* 应用跳动动画 */


}
  
@keyframes jump {
    0% {
      transform: translateY(0);
    }
    50% {
      transform: translateY(-10px);
    }
    100% {
      transform: translateY(0);
    }
  }
  