.ant-tabs .ant-tabs-tab+.ant-tabs-tab {
    margin:0px;
    padding: 0 0 0 15px;
}
.head-info {
    display: flex;
    align-items: center;
}


.head-right  {
    line-height:24px;
}

.head-right p {
    line-height:24px;
    font-size: 10px;
}
.user-head-desc p{
    line-height:24px; 
}

.head-msg {
    width: 130px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space:nowrap;
			
    margin: 0px;
}

.head:hover {
    cursor: default;
    transform: scale(1.01);
    transform-origin: 0 0;
    border-radius: 4px;
    background-color: #EAEAEA;
}


.headSelect {
    cursor: default;
    transform: scale(1.01);
    transform-origin: 0 0;
    border-radius: 4px;
    background-color: #EAEAEA;
}
.head:checked {
    cursor: default;
    transform: scale(1.01);
    transform-origin: 0 0;
    border-radius: 4px;
    background-color: #EAEAEA;
    box-shadow: 2px 2px 5px #cccccc;
}

.head-checked {
    cursor: default;
    transform: scale(1.01);
    transform-origin: 0 0;
    border-radius: 4px;
    background-color: #EAEAEA;
    box-shadow: 2px 2px 5px #cccccc;
}


.head {
    height: 100%;
    padding: 0px 0px 5px 5px;
}


.user-tab-infos::-webkit-scrollbar {
    display: none;
    width: 0;
}

.user-head-desc{
    width: 100%;
    display: flex;
    flex-direction:row;
}
.user-head-desc p{
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
    margin:0px;
}

