.chat-all {
    display: flex;
    flex-direction: row;
}

.chat-content {
    flex: 1;
}

.chat-siber {
    height: 100%;
    width: 240px;
}

.chat-top {
    overflow: hidden;
    flex: 1;
}

.chat-send {
    height: 170px;
}


.ant-card-body::-webkit-scrollbar {
    display: none;
}


.clamp {
    overflow: hidden;
    text-overflow: ellipsis;
    word-break: break-all;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    line-height: 1.2em;
    max-height: 2.4em;
    flex: 1, ;
    width: 260px;
    white-space: pre-wrap;
    word-wrap: break-word;
}

.scroll::-webkit-scrollbar {
    display: none;
  }